.description {
    &__wrap {
        max-width: 714px;
        margin: 0 auto; }
    position: relative;
    &__count {
        text-transform: uppercase;
        color: #007DFF; }
    &__title {
        margin: 16px 0 24px;
        font-weight: 700;
        font-size: 3.5rem;
        line-height: 4.25rem;
        span {
            color: #007DFF; } } }

@media screen and (max-width: 480px) {
    .header {
        .description {
            &__title {
                font-size: 2.6rem;
                line-height: 3.4rem; } } }
    .description {
        &__title {
            font-size: 1.7rem;
            margin-top: 8px;
            line-height: 2.3rem; }
        &__text {
            font-size: 1rem;
            line-height: 1.4rem; }
        &__count {
            font-size: 1rem; } } }

