nav {
    border-bottom: 1px solid #E6EDF0; }
.nav {
    padding: 22px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__start {
        display: flex;
        align-items: center;
        img {
            display: block; } }
    &__end {
        display: flex;
        align-items: center; } }
.logo {
    width: 194px;
    img {
        width: 100%; } }
.menu {
    display: flex;
    margin-left: 70px;
    .menu__item {
        margin: 0 14px;
        transition: .2s linear;
        &:hover {
            color: #007DFF; } } }
.log-in {
    width: 94px;
    height: 48px;
    border: 1px solid #007DFF;
    border-radius: 12px;
    background-color: #fff;
    color: #007DFF;
    font-size: 1rem;
    margin-right: 28px; }

@media screen and (max-width: 850px) {
    .menu {
        display: none; } }

@media screen and (max-width: 480px) {
    .log-in {
        width: 60px;
        height: 32px;
        margin-right: 8px;
        border-radius: 8px;
        font-size: .85rem; }
    .logo {
        width: 135px; }
    .lang__arrow {
        margin-left: 3px; }
    .nav {
        &__end {
            font-size: .85rem; } } }

@media screen and (max-width: 320px) {
    .nav {
        flex-direction: column;
        &__start {
            margin-bottom: 12px; } } }
